.header__entry {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  /* identical to box height, or 122% */
  text-align: right;
  color: #ffffff;
  text-decoration: none;
}

@media (max-width: 768px) {
  .header__entry {
    font-size: 14px;
    line-height: 17px;
  }
}
